.SplashPage {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  width: 100%;
  -webkit-perspective: 1px;
          perspective: 1px;
  background: 
  /* top, transparent red, faked with gradient */ 
  -webkit-gradient(
    linear, 
    left top, left bottom, 
    from(rgba(0, 0, 0, 0.90)), 
    to(rgba(0, 0, 0, 0.90))
  ),
  url('/background.jpg');
  background: 
  /* top, transparent red, faked with gradient */ 
  -webkit-linear-gradient(
    rgba(0, 0, 0, 0.90), 
    rgba(0, 0, 0, 0.90)
  ),
  url('/background.jpg');
  background: 
  /* top, transparent red, faked with gradient */ 
  -o-linear-gradient(
    rgba(0, 0, 0, 0.90), 
    rgba(0, 0, 0, 0.90)
  ),
  url('/background.jpg');
  background: 
  /* top, transparent red, faked with gradient */ 
  linear-gradient(
    rgba(0, 0, 0, 0.90), 
    rgba(0, 0, 0, 0.90)
  ),
  url('/background.jpg');  
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

.SplashPage .parallax {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  position: relative;
  height: 100vh;
  -webkit-transform: translateZ(-1px) scale(2);
          transform: translateZ(-1px) scale(2);
  z-index: -1;
}

.SplashPage .letter {
  display: inline-block;
  font-size: 150px;
  font-weight: 300;
  text-transform: uppercase;
  margin: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
}

.SplashPage .down {
  cursor: pointer;
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

.title:hover .letter {
  margin: 0 30px;
}

.title:hover .down {
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);
}


